.loading{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #222A;
    height: 200px;
    width: 200px;
    border-radius: 50%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;


    z-index: 255;
}

.loadingText{
    color: white;
    font-weight: 900;
}

.loadingImage{
    -webkit-animation: bounce 1s infinite linear;
            animation: bounce 1s infinite linear;
    height: 150px;
    width: 150px;
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
    0%, 100% {-webkit-transform:  scale(1, 1);transform:  scale(1, 1);}
    20%, 50%, 80% {-webkit-transform:  scale(1.2, 0.85);transform:  scale(1.2, 0.85);}
    40% {-webkit-transform: translateY(-50px) scale(0.85, 1.2);transform: translateY(-50px) scale(0.85, 1.2);}
    60% {-webkit-transform: translateY(-30px) scale(0.85, 1.2);transform: translateY(-30px) scale(0.85, 1.2);}
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
    0%, 100% {-webkit-transform:  scale(1, 1);transform:  scale(1, 1);}
    20%, 50%, 80% {-webkit-transform:  scale(1.2, 0.85);transform:  scale(1.2, 0.85);}
    40% {-webkit-transform: translateY(-50px) scale(0.85, 1.2);transform: translateY(-50px) scale(0.85, 1.2);}
    60% {-webkit-transform: translateY(-30px) scale(0.85, 1.2);transform: translateY(-30px) scale(0.85, 1.2);}
}

